.grid {
  width: 100%;
}
.grid .cell-2 {
    display: inline-block;
    vertical-align: top;
    width: calc(100% / 6);
    padding: 12px;
}
@media screen and (max-width: 960px) and (min-width: 481px) {
.grid .cell-2 {
        width: 25%;
}
}
@media screen and (max-width: 480px) {
.grid .cell-2 {
        width: 50%;
}
}
.grid .cell-3 {
    display: inline-block;
    vertical-align: top;
    width: calc(100% / 4);
    padding: 12px;
}
@media screen and (max-width: 1280px) and (min-width: 961px) {
.grid .cell-3 {
        width: 33%;
}
}
@media screen and (max-width: 960px) and (min-width: 481px) {
.grid .cell-3 {
        width: 50%;
}
}
@media screen and (max-width: 480px) {
.grid .cell-3 {
        width: 80%;
        margin: 20px;
}
}
@media screen and (min-width: 1440px) {
.grid .cell-3 {
        width: 25%;
}
}
.grid .cell-4 {
    display: inline-block;
    vertical-align: top;
    width: calc(100% / 3);
    padding: 12px;
}
@media screen and (max-width: 960px) and (min-width: 481px) {
.grid .cell-4 {
        width: 50%;
}
}
@media screen and (max-width: 480px) {
.grid .cell-4 {
        width: 100%;
}
}
.grid .cell-6 {
    display: inline-block;
    vertical-align: top;
    width: calc(100% / 2);
    padding: 12px;
}
@media screen and (max-width: 960px) and (min-width: 481px) {
.grid .cell-6 {
        width: calc(100%*2 / 3);
}
}
@media screen and (max-width: 480px) {
.grid .cell-6 {
        width: 100%;
}
}
.grid .cell-8 {
    display: inline-block;
    vertical-align: top;
    width: calc(100%*2 / 3);
    padding: 12px;
}
@media screen and (max-width: 960px) and (min-width: 481px) {
.grid .cell-8 {
        width: 100%;
}
}
@media screen and (max-width: 480px) {
