body {
  font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
  font-weight: 400;
  font-height: 400;
  padding: 0;
  margin: 0;
}
.card {
  -webkit-box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.12), 0 3px 4px 0 rgba(0, 0, 0, 0.12), 0 3px 3px -2px rgba(0, 0, 0, 0.12);
  box-shadow: 0 1px 8px 0 rgba(0, 0, 0, 0.12), 0 3px 4px 0 rgba(0, 0, 0, 0.12), 0 3px 3px -2px rgba(0, 0, 0, 0.12);
  padding: 12px;
  border-radius: 3px;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .nav {
    background: #333333;
    height: 100%;
    width: 40px;
    -webkit-transition: width .2s;
    transition: width .2s;
}
#app .nav:hover {
      width: 100px;
}
#app .nav .title {
      width: 100% !important;
}
#app .nav .nav-item {
      width: 100% !important;
}
#app .nav .nav-item-group {
      width: 100%;
}
#app .nav .nav-item-group .nav-item {
        white-space: nowrap;
        display: block;
        width: 100%;
}
#app .sector {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    height: 100%;
}
#app .sector .inner-nav {
      height: 56px;
      background: #1cccc6;
}
#app .sector .content-view {
      overflow-y: scroll !important;
      background: #FFFFFF;
      height: calc(100% - 56px);
}



