
.spinner {
  position: absolute;
  width: 60px;
  height: 60px;
  margin-top: -30px;
  margin-left: -30px;
  border-radius: 50%;
  border: 3px solid lavender;
  border-top: solid coral;
  -webkit-animation: spinner 0.7s linear infinite;
          animation: spinner 0.7s linear infinite;
}
@-webkit-keyframes spinner {
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spinner {
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes pulse {
0% {
    opacity: 1;
}
16.666% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
@keyframes pulse {
0% {
    opacity: 1;
}
16.666% {
    opacity: 1;
}
100% {
    opacity: 0;
}
}
.tri-spinner .triangles {
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  height: 81px;
  width: 90px;
  position: absolute;
  left: 50%;
  top: 50%;
}
.tri-spinner .tri {
  position: absolute;
  -webkit-animation: pulse 750ms ease-in infinite;
  animation: pulse 750ms ease-in infinite;
  border-top: 27px solid #215A6D;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  border-bottom: 0px;
}
.tri-spinner .tri.invert {
    border-top: 0px;
    border-bottom: 27px solid #215A6D;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
}
.tri-spinner .tri:nth-child(1) {
    left: 30px;
}
.tri-spinner .tri:nth-child(2) {
    left: 15px;
    top: 27px;
    -webkit-animation-delay: -125ms;
    animation-delay: -125ms;
}
.tri-spinner .tri:nth-child(3) {
    left: 30px;
    top: 27px;
}
.tri-spinner .tri:nth-child(4) {
    left: 45px;
    top: 27px;
    -webkit-animation-delay: -625ms;
    animation-delay: -625ms;
}
.tri-spinner .tri:nth-child(5) {
    top: 54px;
    -webkit-animation-delay: -250ms;
    animation-delay: -250ms;
}
.tri-spinner .tri:nth-child(6) {
    top: 54px;
    left: 15px;
    -webkit-animation-delay: -250ms;
    animation-delay: -250ms;
}
.tri-spinner .tri:nth-child(7) {
    top: 54px;
    left: 30px;
    -webkit-animation-delay: -375ms;
    animation-delay: -375ms;
}
.tri-spinner .tri:nth-child(8) {
    top: 54px;
    left: 45px;
    -webkit-animation-delay: -500ms;
    animation-delay: -500ms;
}
.tri-spinner .tri:nth-child(9) {
    top: 54px;
    left: 60px;
    -webkit-animation-delay: -500ms;
    animation-delay: -500ms;
}



